@font-face {
  font-family: "overpass";
  font-style: normal;
  font-weight: 200;
  src: url("https://unpkg.com/@patternfly/patternfly@2.40.13/assets/fonts/overpass-webfont/overpass-thin.eot");
  /* IE9 Compat Modes */
  src: url("https://unpkg.com/@patternfly/patternfly@2.40.13/assets/fonts/overpass-webfont/overpass-thin.eot?#iefix")
      format("embedded-opentype"),
    url("https://unpkg.com/@patternfly/patternfly@2.40.13/assets/fonts/overpass-webfont/overpass-thin.woff2")
      format("woff2"),
    url("https://unpkg.com/@patternfly/patternfly@2.40.13/assets/fonts/overpass-webfont/overpass-thin.woff")
      format("woff"),
    url("https://unpkg.com/@patternfly/patternfly@2.40.13/assets/fonts/overpass-webfont/overpass-thin.ttf")
      format("truetype");
  /* Safari, Android, iOS */
}

